@mixin block-position {
  width: 100%;
  max-width: $desktop-max-width;
  margin: 0 auto;
  padding: 30px 25px;

  @media (max-width: $tablet-mid-width) {
    padding: 20px 15px;
  }
}

@mixin form-input {
  display: flex;
  flex-direction: column;
  gap: 5px;
  width: 100%;

  @media (max-width: $tablet-max-width) {
    max-width: 500px;
  }
}

@mixin line-trimming($line-clamp) {
  display: -webkit-box;
  -webkit-line-clamp: $line-clamp;
  -webkit-box-orient: vertical;
  overflow: hidden;

  div,
  p {
    display: inline;
  }
}
